@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap");

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
}

h1 {
  color: #f45d48;
  font-family: "Roboto", "Arial Narrow", Arial, sans-serif;
  text-align: left;
}

.App-body h2 {
  color: #222525;
  font-family: "Roboto", "Arial Narrow", Arial, sans-serif;
  font-size: calc(10px + 3.5vmin);
  text-align: left;
  font-weight: 400;
}

.App-body h3 {
  color: #222525;
  font-family: "Roboto", "Arial Narrow", Arial, sans-serif;
  font-size: calc(10px + 2.5vmin);
  text-align: left;
  padding-inline: 0.2em;
  font-weight: 400;
}

.App-body p {
  color: #222525;
  font-family: "Roboto", "Arial Narrow", Arial, sans-serif;
  font-size: calc(10px + 1vmin);
  text-align: left;
  padding-inline: 1em;
  font-weight: 300;
}

p strong {
  font-weight: 500;
}

.Tan {
  background-color: #f8f5f2;
  padding-inline: 4em;
  padding-block: 1em;
}

.White {
  background-color: #ffffff;
  padding-inline: 4em;
  padding-block: 1em;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
